:root {
  --color-white: #FFFFFF;
  --color-black: #000000;
  --color-heading: #171717;
  --color-header-link: #CCCCCC;
  --color-header-border: rgba(255, 255, 255, 0.3);
  --color-header-btn: #20706D;
  --color-static-names: #717171;
  --color-static-shadow: rgba(0, 0, 0, 0.2);
  --color-travel-heading: #171717;
  --color-travel-desc: #404040;
  --color-travel-num: #D4D2D2;
  --color-contacts-bg: #F4F4F4;
  --color-contacts-low-heading: #171717;
  --color-contacts-desc: #404040;
  --color-contacts-border: #8A8A8A;
  --color-contacts-btn-bg: #015D5A;
  --color-footer-text: #015D5A;
  --color-footer-border: rgba(0, 0, 0, 0.3);
}

html {
  scroll-behavior: smooth;
}

body {
  position: relative;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  outline: none;
  font-family: "Gilroy", sans-serif;
}

img {
  max-width: 100%;
}

button {
  border: none;
  background: transparent;
  cursor: pointer;
}

.is_outer {
  position: relative !important;
  left: -9999px !important;
}

.is_hidden {
  display: none !important;
}

.for_mobile {
  display: none;
}
@media (max-width: 1024px) {
  .for_mobile {
    display: block;
  }
}

.error_ {
  border: 2px solid #ff4019 !important;
}

.visualy_hidden {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  overflow: hidden;
}

.heading {
  font-weight: 600;
  font-size: 3.7142857143vw;
  line-height: 4.4285714286vw;
  letter-spacing: -0.02em;
  color: var(--color-heading);
}
.heading span {
  font-weight: 300;
}

.desc {
  font-weight: 300;
  font-size: 1.0714285714vw;
  line-height: 1.3571428571vw;
  color: var(--color-travel-desc);
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 21.4285714286vw;
  height: 4.2857142857vw;
  font-weight: 500;
  font-size: 1.2142857143vw;
  line-height: 1.5vw;
  color: var(--color-header-btn);
  background: var(--color-white);
}

.btn_green {
  color: var(--color-white);
  background: var(--color-contacts-btn-bg);
}

.container {
  max-width: 85vw;
  padding: 0 1.0714285714vw;
  margin: 0 auto;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 5.5714285714vw;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 82.8571428571vw;
  height: 100%;
  padding: 1.7857142857vw 0;
  border-bottom: 0.0714285714vw solid var(--color-header-border);
}

.header__logo {
  font-weight: 600;
  font-size: 1.5714285714vw;
  line-height: 1.8571428571vw;
  color: var(--color-white);
}
.header__logo span {
  font-weight: 400;
}

.nav__list {
  display: flex;
  align-items: center;
}

.nav__item:not(:last-child) {
  margin-right: 4.2857142857vw;
}

.nav__link {
  position: relative;
  font-weight: 300;
  font-size: 1vw;
  line-height: 1.1428571429vw;
  letter-spacing: 0.02em;
  color: var(--color-header-link);
  transition: color 0.25s ease;
}
.nav__link::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: -2.2857142857vw;
  height: 0.1428571429vw;
  background: var(--color-white);
  opacity: 0;
  transform: translateY(100%);
  transition: opacity 0.25s ease, transform 0.25s ease;
}

.nav__link.is_active {
  font-weight: 600;
  color: var(--color-white);
}
.nav__link.is_active::after {
  opacity: 1;
  transform: none;
}

.hero {
  height: 51vw;
  padding: 14.1428571429vw 0 0;
  background: url("../img/hero/bg.png") no-repeat;
  background-size: cover;
  background-position: center;
}

.hero__heading {
  display: flex;
  flex-direction: column;
  max-width: 41.7142857143vw;
  margin: 0;
  margin-bottom: 3.5714285714vw;
  font-weight: 300;
  font-size: 5.8571428571vw;
  line-height: 6.1428571429vw;
  color: var(--color-white);
}
.hero__heading span {
  font-weight: 500;
}

.hero__desc {
  display: inline-block;
  max-width: 16.5714285714vw;
  color: var(--color-white);
  margin-bottom: 3.5714285714vw;
}
.hero__desc:first-of-type {
  margin-right: 3.5714285714vw;
}

.static {
  padding-top: 8.5714285714vw;
  overflow: hidden;
}

.static__container {
  display: flex;
}

.static__left {
  width: 41.2142857143vw;
  margin: -2.5vw 0 0 -3.6428571429vw;
}

.static__values {
  display: flex;
  align-items: center;
  padding: 3.5714285714vw 0;
}

.static__value {
  display: flex;
  flex-direction: column;
}
.static__value:not(:last-child) {
  margin-right: 3.5714285714vw;
}

.static__num {
  font-weight: 500;
  font-size: 2.4285714286vw;
  line-height: 2.8571428571vw;
  color: var(--color-black);
  margin-bottom: 1.0714285714vw;
}

.static__desc {
  font-weight: 300;
  font-size: 1.0714285714vw;
  line-height: 1.2142857143vw;
  color: var(--color-static-names);
}

.static__img {
  position: absolute;
  width: 56.9285714286vw;
  margin-left: -3.7142857143vw;
  margin-top: -2.5714285714vw;
}

.travel {
  padding: 4.2857142857vw;
}

.travel__heading {
  margin-bottom: 5vw;
}

.travel__content {
  display: flex;
}

.travel__list {
  display: flex;
  flex-direction: column;
  max-width: 39.7857142857vw;
  margin-right: 2.8571428571vw;
}

.travel__item {
  position: relative;
  display: flex;
  flex-direction: column;
  padding-left: 1.5714285714vw;
}
.travel__item:not(:last-child) {
  margin-bottom: 2.1428571429vw;
}

.travel__num {
  position: absolute;
  top: 0.2857142857vw;
  left: 0;
  font-weight: 600;
  font-size: 1vw;
  line-height: 1.2142857143vw;
  color: var(--color-travel-num);
}

.travel__low-heading {
  font-weight: 600;
  font-size: 1.5vw;
  line-height: 1.7857142857vw;
  color: var(--color-travel-heading);
  margin-bottom: 1.0714285714vw;
}

.travel__img {
  width: 40vw;
}

.gallery {
  padding: 4.2857142857vw 0;
}

.gallery__heading {
  margin-bottom: 1.7857142857vw;
}

.gallery__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 25.7142857143vw 2.5vw 25.7142857143vw;
  grid-gap: 1.7857142857vw;
}

.gallery__item {
  overflow: hidden;
}

.gallery__item:nth-child(1) {
  grid-column: 1/3;
  grid-row: 1/3;
}

.gallery__item:nth-child(2) {
  grid-row: 1/span 1;
}

.gallery__item:last-child {
  grid-row: 2/span 2;
}

.contacts {
  padding: 3.2142857143vw 0;
  background: var(--color-contacts-bg);
}

.contacts__container {
  display: flex;
  justify-content: space-between;
}

.contacts__heading {
  margin-bottom: 1.0714285714vw;
}

.contacts__list {
  display: flex;
  flex-direction: column;
}

.contacts__item {
  display: flex;
  flex-direction: column;
}
.contacts__item:not(:last-child) {
  margin-bottom: 1.0714285714vw;
}

.contacts__low-heading {
  font-size: 1.2857142857vw;
  line-height: 1.5714285714vw;
  color: var(--color-contacts-low-heading);
  margin-bottom: 1.0714285714vw;
}

.contacts__link {
  font-weight: 300;
  font-size: 1vw;
  line-height: 1.2857142857vw;
  color: var(--color-contacts-desc);
}

.contacts__right {
  display: flex;
  flex-direction: column;
}

.contacts__form-heading {
  margin-bottom: 1.7857142857vw;
}

.contacts__input {
  width: 100%;
  padding: 0.6785714286vw 0;
  border: none;
  background: none;
  border-bottom: 0.0714285714vw solid var(--color-contacts-border);
  font-weight: 300;
  font-size: 1vw;
  line-height: 1.2857142857vw;
  margin-bottom: 1.5714285714vw;
  color: var(--color-contacts-low-heading);
  transition: color 0.25s ease;
}
.contacts__input::placeholder {
  color: var(--color-contacts-desc);
}

.contacts__accept {
  display: block;
  font-weight: 300;
  font-size: 0.8571428571vw;
  line-height: 1.1428571429vw;
  color: var(--color-contacts-desc);
  margin-bottom: 1.0714285714vw;
}
.contacts__accept a {
  color: var(--color-contacts-btn-bg);
  border-bottom: 0.0714285714vw solid var(--color-contacts-btn-bg);
}

.footer {
  height: 4.0714285714vw;
  background: var(--color-white);
}

.footer__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 82.8571428571vw;
  padding: 1.0714285714vw 0;
}

.footer__logo {
  font-weight: 600;
  font-size: 1.5714285714vw;
  line-height: 1.8571428571vw;
  color: var(--color-footer-text);
}
.footer__logo span {
  font-weight: 300;
}

.footer__text {
  font-weight: 300;
  font-size: 0.5714285714vw;
  line-height: 0.6428571429vw;
  color: var(--color-footer-text);
}